body {
  font-family: 'Arial', sans-serif;
  background-color: #FFF8E1; /* Tropical background color */
  color: #333;
  margin: 0;
  padding: 0;
}
header {
  background-color: #4CAF50; /* Green header color */
  padding: 20px;
  text-align: center;
  color: white;
}
section {
  padding: 20px;
}
h2 {
  color: #4CAF50; /* Green text color */
}
form {
  max-width: 600px;
  margin: 0 auto;
}
label {
  display: block;
  margin-bottom: 8px;
}
input, select {
  width: 100%;
  padding: 10px;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
button {
  background-color: #4CAF50; /* Green button color */
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
button:hover {
  background-color: #45A049; /* Darker green on hover */
}
footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}
.login-signup {
  text-align: center;
}
.login-signup input {
  width: 80%;
  padding: 10px;
  margin-bottom: 16px;
}
.login-signup button {
  width: 40%;
}